<template>
  <view class="salon-booking-container" :style="{ paddingTop: navPaddingTop + 'px' }">
    <custom-nav-bar
      title="Á∫ø‰∏ãÈ¶ôÊ∞õÊ≤ôÈæôÈ¢ÑÁ∫¶"
      gradient="linear-gradient(120deg, #880e4f 0%, #c2185b 45%, #e91e63 100%)"
    />
    
    <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
    <view class="loading" v-if="loading">
      <text>Âä†ËΩΩ‰∏≠...</text>
    </view>
    
    <!-- Ê≤ôÈæôÊ¥ªÂä®ÂàóË°® -->
    <view class="salon-list" v-else-if="salons.length > 0">
      <view 
        v-for="salon in salons" 
        :key="salon.id"
        class="salon-card"
      >
        <image :src="salon.image" class="salon-cover" mode="aspectFill"></image>
        <view class="salon-content">
          <text class="salon-title">{{ salon.title }}</text>
          <text class="salon-desc">{{ salon.description }}</text>
          <view class="salon-theme">
            <text class="theme-tag">{{ salon.theme }}</text>
          </view>
          <view class="salon-info">
            <view class="info-item">
              <text class="info-icon">üìÖ</text>
              <text class="info-text">{{ salon.date }} {{ salon.time }}</text>
            </view>
            <view class="info-item">
              <text class="info-icon">üìç</text>
              <text class="info-text">{{ salon.location }}</text>
            </view>
            <view class="info-item">
              <text class="info-icon">üë•</text>
              <text class="info-text" :class="{ 'sold-out': salon.availableSlots === 0 }">
                {{ salon.availableSlots === 0 ? 'Â∑≤Êª°' : `Ââ©‰Ωô ${salon.availableSlots} ‰∏™ÂêçÈ¢ù` }}
              </text>
            </view>
          </view>
          <button 
            class="book-btn"
            :class="{ disabled: salon.availableSlots === 0 }"
            :disabled="salon.availableSlots === 0"
            @click="openBookingForm(salon)"
          >
            {{ salon.availableSlots === 0 ? 'Â∑≤Êª°' : 'Á´ãÂç≥È¢ÑÁ∫¶' }}
          </button>
        </view>
      </view>
    </view>

    <!-- Á©∫Áä∂ÊÄÅ -->
    <view class="empty-state" v-else>
      <text class="empty-icon">üå∏</text>
      <text class="empty-text">ÊöÇÊó†È¶ôÊ∞õÊ≤ôÈæôÊ¥ªÂä®</text>
    </view>

    <!-- È¢ÑÁ∫¶Ë°®ÂçïÂºπÁ™ó -->
    <view class="booking-modal" v-if="showBookingForm" @click.self="closeBookingForm">
      <view class="modal-content">
        <view class="modal-header">
          <text class="modal-title">È¢ÑÁ∫¶‰ø°ÊÅØ</text>
          <text class="close-btn" @click="closeBookingForm">√ó</text>
        </view>
        
        <!-- ÈÄâ‰∏≠ÁöÑÊ≤ôÈæôÊ¥ªÂä®‰ø°ÊÅØ -->
        <view class="selected-salon-info" v-if="selectedSalon">
          <text class="salon-name">{{ selectedSalon.title }}</text>
          <text class="salon-datetime">{{ selectedSalon.date }} {{ selectedSalon.time }}</text>
          <text class="salon-place">{{ selectedSalon.location }}</text>
        </view>
        
        <view class="form-group">
          <text class="form-label">ÂßìÂêç <text class="required">*</text></text>
          <input 
            class="form-input"
            :class="{ error: formErrors.name }"
            v-model="formData.name"
            placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÂßìÂêç"
          />
          <text class="error-text" v-if="formErrors.name">{{ formErrors.name }}</text>
        </view>
        
        <view class="form-group">
          <text class="form-label">ÊâãÊú∫Âè∑ <text class="required">*</text></text>
          <input 
            class="form-input"
            :class="{ error: formErrors.phone }"
            v-model="formData.phone"
            type="number"
            placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÊâãÊú∫Âè∑"
          />
          <text class="error-text" v-if="formErrors.phone">{{ formErrors.phone }}</text>
        </view>
        
        <view class="form-group">
          <text class="form-label">ÂèÇ‰∏é‰∫∫Êï∞</text>
          <input 
            class="form-input"
            v-model="formData.participants"
            type="number"
            placeholder="ÈªòËÆ§1‰∫∫"
          />
        </view>
        
        <view class="form-group">
          <text class="form-label">Â§áÊ≥®</text>
          <textarea 
            class="form-textarea"
            v-model="formData.remark"
            placeholder="Â¶ÇÊúâÁâπÊÆäÈúÄÊ±ÇËØ∑Â§áÊ≥®"
          ></textarea>
        </view>
        
        <button class="submit-btn" @click="submitBooking">Á°ÆËÆ§È¢ÑÁ∫¶</button>
      </view>
    </view>

    <!-- È¢ÑÁ∫¶ÊàêÂäüÁ°ÆËÆ§ÂºπÁ™ó -->
    <view class="booking-modal" v-if="showConfirmation" @click.self="closeConfirmation">
      <view class="modal-content confirmation-modal">
        <view class="success-icon">‚úì</view>
        <text class="success-title">È¢ÑÁ∫¶ÊàêÂäü</text>
        <view class="confirmation-details">
          <view class="detail-item">
            <text class="detail-label">Ê¥ªÂä®ÂêçÁß∞</text>
            <text class="detail-value">{{ confirmationData.salonTitle }}</text>
          </view>
          <view class="detail-item">
            <text class="detail-label">Ê¥ªÂä®Êó∂Èó¥</text>
            <text class="detail-value">{{ confirmationData.salonDate }} {{ confirmationData.salonTime }}</text>
          </view>
          <view class="detail-item">
            <text class="detail-label">Ê¥ªÂä®Âú∞ÁÇπ</text>
            <text class="detail-value">{{ confirmationData.salonLocation }}</text>
          </view>
          <view class="detail-item">
            <text class="detail-label">È¢ÑÁ∫¶‰∫∫</text>
            <text class="detail-value">{{ confirmationData.name }}</text>
          </view>
          <view class="detail-item">
            <text class="detail-label">ËÅîÁ≥ªÁîµËØù</text>
            <text class="detail-value">{{ confirmationData.phone }}</text>
          </view>
          <view class="detail-item">
            <text class="detail-label">ÂèÇ‰∏é‰∫∫Êï∞</text>
            <text class="detail-value">{{ confirmationData.participants }}‰∫∫</text>
          </view>
        </view>
        <view class="confirmation-actions">
          <button class="view-records-btn" @click="goToRecordsFromConfirm">Êü•ÁúãÊàëÁöÑÈ¢ÑÁ∫¶</button>
          <button class="close-confirm-btn" @click="closeConfirmation">ËøîÂõûÊ¥ªÂä®ÂàóË°®</button>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import CustomNavBar from '@/components/CustomNavBar.vue'
import AromaBookingService from '@/services/aroma-booking.service.js'

export default {
  components: { CustomNavBar },
  data() {
    return {
      navPaddingTop: 100,
      salons: [],
      selectedSalon: null,
      showBookingForm: false,
      showConfirmation: false,
      loading: true,
      confirmationData: {
        salonTitle: '',
        salonDate: '',
        salonTime: '',
        salonLocation: '',
        name: '',
        phone: '',
        participants: 1
      },
      formData: {
        name: '',
        phone: '',
        participants: 1,
        remark: ''
      },
      formErrors: {}
    }
  },
  onLoad() {
    const info = uni.getSystemInfoSync()
    this.navPaddingTop = (info.statusBarHeight || 20) + 75
    this.loadSalons()
  },
  methods: {
    /**
     * Âä†ËΩΩÈ¶ôÊ∞õÊ≤ôÈæôÊ¥ªÂä®ÂàóË°®
     */
    async loadSalons() {
      this.loading = true
      try {
        this.salons = await AromaBookingService.getSalons()
      } catch (err) {
        console.error('Failed to load salons:', err)
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        })
      } finally {
        this.loading = false
      }
    },
    
    /**
     * Ë∑≥ËΩ¨Âà∞È¢ÑÁ∫¶ËÆ∞ÂΩïÈ°µÈù¢
     */
    goToRecords() {
      uni.navigateTo({
        url: '/pages/aroma-healing/booking-records'
      })
    },
    
    /**
     * ÊâìÂºÄÈ¢ÑÁ∫¶Ë°®ÂçïÂºπÁ™ó
     * @param {Object} salon - È¶ôÊ∞õÊ≤ôÈæôÊ¥ªÂä®ÂØπË±°
     */
    openBookingForm(salon) {
      this.selectedSalon = salon
      this.showBookingForm = true
      this.resetForm()
    },
    
    /**
     * ÂÖ≥Èó≠È¢ÑÁ∫¶Ë°®ÂçïÂºπÁ™ó
     */
    closeBookingForm() {
      this.showBookingForm = false
      this.selectedSalon = null
      this.resetForm()
    },
    
    /**
     * ÈáçÁΩÆË°®ÂçïÊï∞ÊçÆ
     */
    resetForm() {
      this.formData = {
        name: '',
        phone: '',
        participants: 1,
        remark: ''
      }
      this.formErrors = {}
    },

    /**
     * È™åËØÅË°®ÂçïÊï∞ÊçÆ
     * @returns {boolean}
     */
    validateForm() {
      const result = AromaBookingService.validateAromaBookingForm(this.formData)
      
      // ËΩ¨Êç¢ÈîôËØØÊ†ºÂºè
      const errors = {}
      if (!result.valid) {
        result.errors.forEach(err => {
          errors[err.field] = err.message
        })
      }
      
      this.formErrors = errors
      return result.valid
    },
    
    /**
     * ÂÖ≥Èó≠Á°ÆËÆ§ÂºπÁ™ó
     */
    closeConfirmation() {
      this.showConfirmation = false
      this.confirmationData = {
        salonTitle: '',
        salonDate: '',
        salonTime: '',
        salonLocation: '',
        name: '',
        phone: '',
        participants: 1
      }
    },
    
    /**
     * ‰ªéÁ°ÆËÆ§ÂºπÁ™óË∑≥ËΩ¨Âà∞È¢ÑÁ∫¶ËÆ∞ÂΩï
     */
    goToRecordsFromConfirm() {
      this.closeConfirmation()
      this.goToRecords()
    },
    
    /**
     * Êèê‰∫§È¢ÑÁ∫¶
     */
    async submitBooking() {
      if (!this.validateForm()) {
        return
      }
      
      if (!this.selectedSalon) {
        uni.showToast({
          title: 'ËØ∑ÈÄâÊã©Ê¥ªÂä®',
          icon: 'none'
        })
        return
      }
      
      const result = await AromaBookingService.createAromaBooking(
        this.selectedSalon.id,
        this.selectedSalon,
        this.formData
      )
      
      if (result.success) {
        // Êõ¥Êñ∞Êú¨Âú∞Ê¥ªÂä®Ââ©‰ΩôÂêçÈ¢ù
        const salonIndex = this.salons.findIndex(s => s.id === this.selectedSalon.id)
        if (salonIndex !== -1) {
          this.salons[salonIndex].availableSlots -= (this.formData.participants || 1)
        }
        
        // ‰øùÂ≠òÁ°ÆËÆ§‰ø°ÊÅØ
        this.confirmationData = {
          salonTitle: this.selectedSalon.title,
          salonDate: this.selectedSalon.date,
          salonTime: this.selectedSalon.time,
          salonLocation: this.selectedSalon.location,
          name: this.formData.name,
          phone: this.formData.phone,
          participants: this.formData.participants || 1
        }
        
        // ÂÖ≥Èó≠Ë°®ÂçïÔºåÊòæÁ§∫Á°ÆËÆ§ÂºπÁ™ó
        this.showBookingForm = false
        this.showConfirmation = true
        this.selectedSalon = null
        this.resetForm()
      } else {
        uni.showToast({
          title: result.message || 'È¢ÑÁ∫¶Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        })
      }
    }
  }
}
</script>

